nav {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #fff;
  padding: 0 20px;
  width: 100%;
}
nav .nav-logo img {
  width: 90px;
  height: 90px;
  border-radius: 50%;
  margin-top: 30px;
  border: 4px solid #fcb212;
  -webkit-user-drag: none;
}
nav ul {
  display: flex;
  list-style-type: none;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
nav ul li {
  border-radius: 4px;
  transition: all 0.3s ease-in-out;
  padding-bottom: 5px;
}
nav ul li a {
  text-decoration: none;
  color: #333;
}
nav ul li:hover {
  background-color: #fcb212;
  padding: 5px;
}

/* media query */

@media (min-width: 768px) {
  nav ul {
    flex-direction: row;
  }
  nav ul li {
    padding: 10px;
  }
}
