.services {
  margin: 20px;
  background-color: #fff;
}
.services-list-container {
  border-radius: 5px;
  padding: 20px;
}
.service-item {
  margin: 20px;
  border-radius: 16px;
  padding: 20px;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
}
.service-item i {
  font-size: 2rem;
  margin: 10px;
}

.fa-code {
  color: #e94c89;
}
.fa-desktop {
  color: #fdb448;
}
.fa-tablet-alt {
  color: #c31f0c;
}
.services-description-container {
  display: "flex";
  flex-direction: column;
  align-items: flex-start;
  margin: 20px;
}
.services h1 span {
  font-size: 2.5rem;

  color: #fdb448;
}
.services button {
  background-color: #fdb448;
  border: none;
  border-radius: 5px;
  color: #fff;
  margin-bottom: 30px;
  margin-top: 20px;
  width: 120px;
  padding: 10px;
  font-size: 1.5rem;
}

/* media query */
@media (min-width: 768px) {
  .services-list-container {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
 .service-description-container {
    width: 40%;
  }
   .service-item{
    width: 80%;
  }
  .service-item-container {
    width: 60%;
  }  
}
