.footer {
  width: 100%;
  background-color: white;
  /* color: white; */
  padding: 20px;
  margin-top: 40px;
  box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.1), -2px -2px 2px rgba(0, 0, 0, 0.1);
}
.footer-info {
  width: 80%;
  margin: auto;
  display: flex;
  align-items: center;
  justify-content: space-around;
}
.footer-info ul li {
  display: inline-block;
  list-style: none;
  margin: 20px;
}
.footer-info ul li a {
  text-decoration: none;
}
/* media query */
@media (max-width: 772px) {
  .footer-info {
    width: 90%;
  }
  .footer-info ul {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }
  .footer-info ul li {
    margin: 10px;
  }
}
