.works-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  height: 100%;
  padding: 0 10px;
}
.works-item {
  position: relative;
  transition: all 0.72s;
}
.works-item img {
  width: 100%;
  height: 100%;
  aspect-ratio: 3/2;
  border-radius: 5px;
}
.works-item a {
  text-decoration: none;
}

.icon {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  background-color: rgba(0, 15, 152, 0.3);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
  opacity: 0;
  padding-top: 70%;
  transition: 0.4s;
  border-radius: 10px;
}
.icon h2 {
  color: white;
}
.icon .demo {
  width: 200px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 30px;
}
.demo button {
  background-color: black;
  padding: 8px 10px;
  width: 80px;
  font-size: 18px;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}
.demo button a{
  text-decoration: none;
  color: white;
}
.works-item:hover .icon {
  opacity: 1;
  padding-top: 0;
}

/* --------media query------------ */

@media (min-width: 768px) {
  .works-list-container {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
  }
  .works-item {
    width: 30%;
    margin: 10px;
  }
}
